import React from 'react';
import { useMotionValue, useTransform, motion } from 'framer-motion';

function randSign() {
  return (Math.random() > 0.5) ? 1 : -1;
}

function Frond({ offset, segLength, numSegs, containerHeight }) {
  const segs = [];
  for (let i = 0; i < numSegs; i++) {
    segs.push({ length: segLength + segLength * Math.random(), span: randSign() * (0.1 + 0.2 * Math.random()) * segLength });
  }

  const t = useMotionValue(0);
  const path = useTransform(t, (current) => segs.reduce((path, { length, span }, i) => {
    if (i > 0) path += 's ';
    path += `${(current + 0.1) * span},${0.67 * length} ${0.2 * current * span},${length} `;
    return path;
  }, 'M 0,0 c 0,0 '));

  return (
    <motion.path
      className="frond"
      style={{t, d: path, strokeWidth: segLength * 0.8, x: `${offset}%`, y: -segLength * 0.5}}
      animate={{t: [-1, 1]}}
      transition={{ease: 'easeInOut', yoyo: Infinity, duration: 2 + Math.random()}}
    />
  );
}

export default function Seaweed({ containerHeight, containerWidth }) {
  return (
    <svg className="seaweed" style={{width: containerWidth, height: containerHeight}}>
      <Frond offset={4} segLength={containerHeight * 0.07} numSegs={5} containerHeight={containerHeight} />
      <Frond offset={17} segLength={containerHeight * 0.07} numSegs={6} containerHeight={containerHeight} />
      <Frond offset={22} segLength={containerHeight * 0.07} numSegs={4} containerHeight={containerHeight} />
      <Frond offset={38} segLength={containerHeight * 0.07} numSegs={5} containerHeight={containerHeight} />
      <Frond offset={51} segLength={containerHeight * 0.07} numSegs={3} containerHeight={containerHeight} />
      <Frond offset={60} segLength={containerHeight * 0.07} numSegs={5} containerHeight={containerHeight} />
      <Frond offset={66} segLength={containerHeight * 0.07} numSegs={8} containerHeight={containerHeight} />
      <Frond offset={80} segLength={containerHeight * 0.07} numSegs={6} containerHeight={containerHeight} />
      <Frond offset={92} segLength={containerHeight * 0.07} numSegs={4} containerHeight={containerHeight} />
    </svg>
  );
}